import React from 'react';
import { ThemeProvider } from '../../providers/ThemeContext';
import ThemedWrapper from '../theme/ThemedWrapper';

const ThemeHOC = ({ children }) => {
    return (
        <ThemeProvider>
            <ThemedWrapper>
                {children}
            </ThemedWrapper>
        </ThemeProvider>
    );
};

export default ThemeHOC;