import React from "react";
import { projectList } from "../data";
import {Navigate, useParams} from "react-router-dom";
const MyProjects = () => {
	const { id } = useParams();
	const projectId = projectList[id];

	if(!projectId) {
		return <Navigate to="/" replace/>
	}

	return (
		<main className="section">
			<div className="container">
				<div className="project-details">
					<h1 className="title-1">{projectId.title}</h1>

					<img
						src={projectId.imgBig}
						alt={projectId.title}
						className="project-details__cover"
					/>

					<div className="project-details__desc">
						<p>About: {projectId.about}</p>
					</div>

					<a href={projectId.projectLink} className="btn" target={projectId.target ?? "_blank"}>
						<span className="text">Open the website</span>
					</a>
				</div>
			</div>
		</main>
	);
};

export default MyProjects;
