import { useRef, useState, useEffect } from "react";

// Custom hook for animating text with repeat
function useTextAnimation(initialText, delay = 3000) {
	const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
	const [animatedText, setAnimatedText] = useState(initialText);
	const intervalRef = useRef(null);

	const startAnimation = () => {
		let iteration = 0;

		clearInterval(intervalRef.current);

		intervalRef.current = setInterval(() => {
			setAnimatedText((prevText) =>
				prevText
					.split("")
					.map((letter, index) => {
						if (index < iteration) {
							return initialText[index];
						}

						return letters[Math.floor(Math.random() * 26)];
					})
					.join("")
			);

			if (iteration >= initialText.length) {
				clearInterval(intervalRef.current);
				setTimeout(startAnimation, 5000); // Start animation again after 5 seconds
			}

			iteration += 1 / 3;
		}, 45);
	};

	useEffect(() => {
		const timeout = setTimeout(() => {
			startAnimation();
		}, delay);

		return () => {
			clearInterval(intervalRef.current);
			clearTimeout(timeout);
		};
	}, [delay]); // Re-run effect if delay changes

	return { animatedText };
}

export default useTextAnimation;
