import React, {useEffect, useState} from "react";
import "./styles/main.css";
import {Routes, Route, Navigate} from "react-router-dom";

import Home from "./pages/Home";
import Projects from "./pages/Projects";
import Contacts from "./pages/Contacts";
import MyProjects from "./pages/MyProjects";
import SkillsAndCertificates from "./pages/SkillsAndCertificates";

import {Loyout} from "./components/loyout/Loyout";
import Loader from "./components/loader/Loader";

function App() {
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        let timeoutID = setTimeout(() => {
            setLoading(false);
        }, 2500);
        return () => clearTimeout(timeoutID);
    }, []);

    return (
        <div className="App">
            <div className="scrollbar" id="custom-scroll">
                <div className="force-overflow">
                    {loading ? (
                        <Loader/>
                    ) : (
                        <Routes>
                            <Route path="/" element={<Loyout/>}>
                                <Route index element={<Home/>}/>
                                <Route path="projects" element={<Projects/>}/>
                                <Route path="projects/myproject/:id" element={<MyProjects/>}/>
                                <Route path="skillsAndCertificates" element={<SkillsAndCertificates/>}/>
                                <Route path="contacts" element={<Contacts/>}/>
                                <Route path="*" element={<Navigate to="/" replace/>}/>
                            </Route>
                        </Routes>
                    )}
                </div>
            </div>
        </div>
    );
}

export default App;
