import React, { useState, useEffect, useRef } from "react";
import "./style.css";

import { NavLink } from "react-router-dom";
import { navLinks } from "../../data";
import { FaBars, FaTimes } from 'react-icons/fa';
import DarkModeButton from '../theme/DarkModeButton';

const setActive = ({ isActive }) =>
	isActive ? "nav-list__link nav-list__link--active" : "nav-list__link";
const Navbar = () => {
	const [isOpen, setIsOpen] = useState(false);
	const navRef = useRef(null);

	const toggleMenu = () => {
		setIsOpen(!isOpen);
	};

	useEffect(() => {
		const handleClickOutside = (event) => {
			if (navRef.current && !navRef.current.contains(event.target)) {
				setIsOpen(false);
			}
		};

		document.addEventListener("mousedown", handleClickOutside);
		return () => {
			document.removeEventListener("mousedown", handleClickOutside);
		};
	}, []);

	return (
		<nav className="nav">
			<div className="container">
				<div className="nav-row">
					<NavLink to="/" className="logo" onClick={() => setIsOpen(false)}>
						<strong>Narine's</strong> portfolio
					</NavLink>
					<div className="burger-icon" onClick={toggleMenu}>
						{isOpen ? <FaTimes /> : <FaBars />}
					</div>
					<ul className={`nav-list ${isOpen ? 'open' : ''}`} ref={navRef}>
						<div className="burger-icon" onClick={toggleMenu}>
							{isOpen ? <FaTimes/> : <FaBars/>}
						</div>
						{navLinks?.map((navLink) => (
							<li className="nav-list__item" key={navLink.id}>
								<NavLink
									key={navLink.id}
									to={navLink.to}
									className={setActive}
									onClick={() => setIsOpen(false)}
								>
									{navLink.label}
								</NavLink>
							</li>
						))}
						<li className="nav-list__item">
							<DarkModeButton/>
						</li>
					</ul>
				</div>
			</div>
		</nav>
	);
};

export default Navbar;
