import React from "react";
import "./style.css";
import useTextAnimation from "../hooks/useTextAnimation";
import CV_Narine_Mirzoyan from "../../resources/CV_Narine_Mirzoyan.pdf"

const Header = () => {
	const { animatedText } = useTextAnimation("NARINE", 4000);
	return (
		<header className="header">
			<div className="header__wrapper">
				<h1 className="header__title"> 
				<strong >
						Hi, my name is <br /> <em>{animatedText}</em>
				</strong>
					<br />I am a financial risks management specialist
				</h1>
				<div className="header__text">
					<p>with passion for learning and creating</p>
				</div>
				<a href={CV_Narine_Mirzoyan} className="btn" download="CV_Narine_Mirzoyan.pdf">
					Download CV
				</a>
			</div>
		</header>
	);
};

export default Header;
