import React from "react";
import "./loader.css";

const Loader = () => {
	return (
		<div className="loading-container">
			<div className="loading-text">
				<span>L</span>
				<span>O</span>
				<span>A</span>
				<span>D</span>
				<span>I</span>
				<span>N</span>
				<span>G</span>
			</div>
			<div className="loading-line"></div>
		</div>
	);
};

export default Loader;
