import React, { createContext, useState, useContext } from 'react';

// Create a context for the theme
const ThemeContext = createContext(undefined);

// Safe JSON parse function
const safeJSONParse = (value, defaultValue) => {
    try {
        return JSON.parse(value);
    } catch (e) {
        return defaultValue;
    }
};

// Create a provider for the theme
export const ThemeProvider = ({ children }) => {
    const [isDarkMode, setIsDarkMode] = useState(() => {
        const savedTheme = localStorage.getItem('NarineCVisDarkMode');
        let parsedTheme = safeJSONParse(savedTheme, false);

        // If parsedTheme is not a boolean, reset it to false
        if (typeof parsedTheme !== 'boolean') {
            parsedTheme = false;
            localStorage.setItem('NarineCVisDarkMode', JSON.stringify(parsedTheme));
        }

        return parsedTheme;
    });

    // Function to toggle the theme
    const toggleDarkMode = () => {
        setIsDarkMode((prevMode) => {
            const newMode = !prevMode;
            localStorage.setItem('NarineCVisDarkMode', JSON.stringify(newMode)); // Save to local storage
            return newMode;
        });
    };

    return (
        <ThemeContext.Provider value={{ isDarkMode, toggleDarkMode }}>
            {children}
        </ThemeContext.Provider>
    );
};

// Custom hook to consume the theme context
export const useTheme = () => {
    return useContext(ThemeContext);
};